.hero-container {
  position: relative;
  text-align: center;
  background: radial-gradient(circle at center, rgba(255, 255, 255, 0) 60%, #81A5FE 60%);
  /* Additional styling for your container */
}

.profile-image {
  width: 200px; /* Adjust the size as needed */
  height: auto; /* Maintain aspect ratio */
  border-radius: 50%; /* Makes the image round */
  position: absolute;
  left: 50%; /* Center the image horizontally */
  top: 50%; /* Center the image vertically */
  transform: translate(-50%, -50%); /* Adjust the position correctly */
  z-index: 1; /* Ensure the image is above other elements */
}

.title-container {
  position: relative;
  z-index: 2; /* Ensure the text is above the image */
  /* Additional styling for your title container */
}


.title-container {
  position: absolute; /* Or 'fixed' for fixed positioning */
  top: 50%; /* Position the top of the container at the vertical center */
  left: 50%; /* Position the left of the container at the horizontal center */
  transform: translate(-50%, -50%); /* Offset the container */
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
}

.main-title {
  font-size: 2rem; /* Large font size for the title */
  width: 80%; /* Increased width */
  margin: 0 auto; /* Center the title block horizontally */
  text-align: center; /* Center the text within the title block */
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  color: #2f2f63;
  /* Other styling as needed */
}

.profile-image {
  width: 200px; /* Set the width as needed */
  height: auto; /* Maintain aspect ratio */
  border-radius: 50%; /* Make it round */
  position: absolute; /* Position it over the gradient */
  left: 50%; /* Center horizontally */
  top: 50%; /* Center vertically */
  transform: translate(-50%, -50%); /* Adjust for the size of the image */
}

